import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import moment from 'moment';
import {isEmpty} from 'lodash';
import 'bootstrap/dist/css/bootstrap.css';
import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

class ThumbnailList extends Component {
  constructor() {
    super();
    this.state = {
      imageList: []
    }
  }

  componentDidMount() {
    this.setState({
      imageList: this.props.dataList
    })
  }
  // componentWillUnmount() { 
  // }

  getPreText(item) {
    let contentText = item.content;
    contentText = contentText.replace(/<IMG(.*?)>/gi, '');
    contentText = contentText.replace(/(<([^>]+)>)/ig,"");
    contentText = contentText.replace(/\r\n|\n/g, '');
    return contentText;
  }
  render() {
    return (
      <div>
        <Row xs={2} md={6} className="g-3">
         {this.state.imageList.map((data,index) =>
              <Col key={index}>
              <Card style={{width:'100%'}}>
                <Card.Header style={{fontFamily: 'GothicB'}}>
                  <h5>{data.subject}</h5>
                </Card.Header>
                <Link to={{pathname:`/posts/detail`, search: `?id=${data.id}&groupType=${data.group_type}`}} className="btn-text-s">
                  <img src={data.image} className="card-img" alt="" style={{maxHeight: ''}} />
                </Link>
                <Card.Body>
                  <Card.Text className="card-short-text">
                    {this.getPreText(data)}
                  </Card.Text>
                </Card.Body>
                {/* <div style={{padding: '10px'}}>
                <button type="button" className="btn btn-outline-secondary btn-sm btn-br mr-10">
                  <Link to={{pathname:`/posts/detail`, search: `?id=${data.id}&groupType=${data.group_type}`}} className="btn-text-s">read more</Link>
                </button>
                </div> */}
              </Card>
            </Col>
         )}
        </Row>
      </div>
    )
  }
}

export default ThumbnailList;